.Drawerext {
    background-color: antiquewhite;
    position: relative;
    margin-top: 312px;
    width: 200px;
    left: -46px;
    transition: display 0.05s;
    -webkit-transition: display 0.05s;
    -moz-transition: display 0.05s;
    -ms-transition: display 0.05s;
    -o-transition: display 0.05s;
    color: brown;
    list-style: none;
    font-size: 1.08rem;
    -webkit-box-shadow: 3px 3px 8px #9c9494;
            box-shadow: 3px 3px 8px #9c9494;
    border-radius: 5px;
    z-index: 2;
}

.Open {
    display: block;
}

.Close {
    display: none;
}

@media (max-width: 1300px) {
    .Drawerext {
        font-size: 0.7rem;
        width: 140px;
        left: -70px;
        margin-top: 280px;
    }
}


@media (max-width: 1100px) {
    .Drawerext {
        font-size: 0.6rem; 
        width: 140px;
        left: -70px;
    }
}

@media (max-width: 950px) {
    .Drawerext {
        font-size: 0.5rem;
        width: 100px;
    }
}

@media (max-width: 800px) {
    .Drawerext {
        font-size: 0.4rem; 
        width: 150px;
        left: -80px;
        margin-top: 280px;
    }
}


