.Header {
    height: 56px;
    background-color: rgb(219, 97, 97);
    top: 0;
    left: 0;
    padding: 0px 20px;
    width: 100%;
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-weight: bold;
    font-family: 'Lato', sans-serif;
    font-size: 1.6rem;
    color: antiquewhite;
}


@media (max-width: 950px) {
    .Header {
        height: 100px;
    }
} 


